@Component({
    selector: 'app-bo-creator-modal',
    templateUrl: './bo-creator-modal.component.html',
    styleUrls: ['./bo-creator-modal.component.css']
})
export class BoCreatorModalComponent implements OnInit {

    parentId:string;

    @Input() set _parentId(parentId:string){
        this.parentId=parentId;
        this.getParentBo();
    }

    @Output() boConfirmEmitter: EventEmitter<any> = new EventEmitter<any>();

    @ViewChild("boGroupSelector") boGroupSelector: ComboListComponent;
    
    /**
     * å½åBOèç¹
     */
    currentBo: BusinessObjectEntity = new BusinessObjectEntity();

    /**
     * å³é®åºç¨éå
     */
    items: BusinessObjectEntity[]=[];

    /**
     * éæ©åºç¨èç¹ä¸çæææå¡ååéå
     */
    suList: BusinessObjectEntity[]=[];

    /**
     * å½åéæ©çSUä¸çææBO
     */
    bosInSU: BusinessObjectEntity[];

    /**
     * ææä¸å¡å¯¹è±¡èç¹éå
     */
    data:any[]=[];

    /**
     * æå±å¾®æå¡
     */
    suCode:string;

    parentBo: BusinessObjectEntity=new BusinessObjectEntity();

    labelLength = '2';
    inputLength = '9';

    constructor(
        private boCreatorUtils:BoCreatorUtils,
        private boDtService: BusinessObjectDtService,
        private notifyService: NotifyService) { }

    ngOnInit() {
        this.initData();
    }

    private initData() {
        this.boDtService.getBusinessObjects().subscribe(data => {
            this.data = data;
            this.items = data.filter(item => item.parentID === this.parentId && item.layer === 4 && item.isDetail === '0');
            const children = data.filter(item => item.parentID === this.parentId);
            let boInSU = [];
            children.forEach(s => {
                if (s.isDetail == '0') {
                    boInSU.push(s);
                    var bos = data.filter(t => t.parentID === s.id);
                    if (bos) {
                        bos.forEach(bo => {
                            boInSU.push(bo);
                        });
                    }
                }
                else {
                    boInSU.push(s);
                }
            });
            this.bosInSU = boInSU;
        });
    }

    create(){
        let bo=this.getCurrentBo();
        if(!bo){
            return;
        }
        this.boDtService.save(bo).subscribe(
            () => {
                this.boConfirmEmitter.emit(bo);
            },
            error => {
                this.notifyService.error('åå»ºå¤±è´¥');
                throw error;
            }
        );
    }

    getCurrentBo(){
        let result=this.boInfoValidator();
        if(result.illegal){ 
            this.notifyService.warning(result.message);
            return null;
        }
        this.currentBo.id = this.boCreatorUtils.newGuid();
        this.currentBo.layer =  this.parentBo?this.parentBo.layer + 1:4;
        this.currentBo.parentID = this.parentBo&&this.parentBo.id?this.parentBo.id:this.parentId;
        this.currentBo.sysInit = '0';
        this.currentBo.isSysInit = false;
        this.currentBo.isDetail = '1';

        let max = this.boCreatorUtils.getMaxOrder(this.bosInSU, this.currentBo.parentID);
        this.currentBo.sortOrder = max + 1;
        return Object.assign(new BusinessObjectEntity(),this.currentBo);
    }

    boInfoValidator(): ValidatorResult {

        //æ°å¢ä¿¡æ¯ç©ºæ ¡éª
        let result = this.emptyValidator();
        if (result.illegal) {
            return result;
        }

        //ç¼å·ï¼åç§°å½åè§èæ§æ ¡éª
        result = this.namingConventionValidator();
        if (result.illegal) {
            return result;
        }

        //APPç¼å·éå¤æ ¡éª
        return this.repeatValidator();

    }

    emptyValidator(): ValidatorResult {
        let result = new ValidatorResult();
        if(!this.suCode){
            result.illegal = true;
            result.message = `æå±å¾®æå¡ä¸è½ä¸ºç©º`;
            return result;
        }
        if (!this.currentBo.code) {
            result.illegal = true;
            result.message = `ä¸å¡å¯¹è±¡çç¼å·ä¸è½ä¸ºç©º`;
            return result;
        }
        if (!this.currentBo.name) {
            result.illegal = true;
            result.message = `ä¸å¡å¯¹è±¡çåç§°ä¸è½ä¸ºç©º`;
            return result;
        }
        return result;
    }

    namingConventionValidator(): ValidatorResult {
        let result = new ValidatorResult();
        const codeRegx = new RegExp('^[a-zA-Z\x7f-\xff][a-zA-Z0-9\x7f-\xff]*$');
        if (!codeRegx.test(this.currentBo.code)) {
            result.illegal = true;
            result.message = 'ä¸å¡å¯¹è±¡çç¼å·åªè½åå«å­æ¯,æ°å­,å¹¶ä¸ä¸å¯ä»¥ç¨æ°å­å¼å¤´!';
            return result;
        }
        const nameRegx = new RegExp('[`~!@#$^&*()=|{}\':;\',\\[\\].<>/?~ï¼@#ï¿¥â¦â¦&*ï¼ï¼ââ|{}ããâï¼ï¼ââ\'ãï¼ãï¼]');
        if (nameRegx.test(this.currentBo.name)) {
            result.illegal = true;
            result.message = 'ä¸å¡å¯¹è±¡çåç§°åå«éæ³å­ç¬¦ï¼è¯·éæ°è¾å¥!';
            return result;
        }
        return result;
    }

    repeatValidator(): ValidatorResult {
        let result = new ValidatorResult();
        if (!this.bosInSU|| this.bosInSU.length <= 0) {
            return result;
        }
        const repeatCodeBO = this.bosInSU.find(bo => bo.code.toLowerCase() === this.currentBo.code.toLowerCase());
        if (repeatCodeBO) {
            result.illegal = true;
            result.message = 'åä¸SUä¸ä¸å¡å¯¹è±¡/ä¸å¡å¯¹è±¡åç»/ä¸å¡å¯¹è±¡ä¸ä¸å¡å¯¹è±¡åç»ç¼å·ä¸è½éå¤ï¼ä¸å¡å¯¹è±¡ç¼å·ä¸ºï¼' + this.currentBo.code;
            return result;
        }
        return result;
    }

    boGroupSelectChanged($event) {
        this.parentBo = $event.data;
    }

    private getParentBo() {
        if (this.parentId) {
            this.boDtService.getBusinessObject(this.parentId).subscribe(data => {
                this.parentBo = data;
                this.suCode=data.code;
            });
        }
    }

    clearHandler(){
        this.parentBo=null;
    }
}
