import { Component, OnInit, Output, EventEmitter, Input, ViewChild } from '@angular/core';
import { AppCreatorModalComponent } from './modal/app-modal/app-creator-modal.component';
import { SuCreatorModalComponent } from './modal/su-modal/su-creator-modal.component';
import { BoCreatorModalComponent } from './modal/bo-modal/bo-creator-modal.component';

@Component({
    selector: 'app-bo-creator',
    templateUrl: './bo-creator.component.html',
    styleUrls: ['./bo-creator.component.css']
})
export class BOCreatorComponent implements OnInit {
    @Output() createdAfterOk: EventEmitter<any> = new EventEmitter<any>();
    @Output() modalClosed = new EventEmitter<any>();
    @Input() parentId: string;
    @Input() type: string;

    @ViewChild("appModal") appCmp: AppCreatorModalComponent;
    @ViewChild("suModal") suCmp: SuCreatorModalComponent;
    @ViewChild("boModal") boCmp: BoCreatorModalComponent;

    constructor() { }

    ngOnInit() { }

    appConfirmEmitterHandler($event) {
        let bo = $event;
        this.createdAfterOk.emit(bo);
        this.modalClosed.emit();
    }

    suConfirmEmitterHandler($event) {
        let bo = $event;
        this.createdAfterOk.emit(bo);
        this.modalClosed.emit();
    }

    boConfirmEmitterHandler($event) {
        let bo = $event;
        this.createdAfterOk.emit(bo);
        this.modalClosed.emit();
    }

    btnOkClick() {
        switch (this.type) {
            case 'app':
                this.appCmp.create();
                return;
            case 'su':
                this.suCmp.create();
                return;
            case 'bo':
                this.boCmp.create();
                return;
            default:
                return;
        }
    }

    hideModal() {
        this.modalClosed.emit();
    }

}
